@import url('https://fonts.googleapis.com/css?family=Audiowide');

body {
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: 'Audiowide';
    overflow-y: scroll;
}

.options {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100vw;
    height: 90vh;
}

.content-box-div {
    width: 33%;
    height: 80%;
}

@media (max-width: 600px) {
    .options {
        display: inline-block;
        width: 100%;
    }

    .content-box-div {
        margin: auto;
        margin-top: 2.5vh;
        margin-bottom: 2.5vh;
        width: 90%;
    }

}